<template>
    <div class="sales-board">
      <div class="sales-board-intro">
        <h2></h2>
        <p></p>
      </div>
      <div class="sales-board-form">
          <div class="sales-board-line">
              <div class="sales-board-line-left" >
                  
              </div>
              <div class="sales-board-line-right">
                <v-counter @on-change="onParamChange('buyNum',$event)"></v-counter>   
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                 <v-multiply :selections="versionList" @on-change="onParamChange('versions',$event)"></v-multiply> 
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                   <v-chooser :selections="timeList" @on-change="onParamChange('time',$event)"></v-chooser>
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">
                  
              </div>
              <div class="sales-board-line-right">
                 {{price}}
              </div>
          </div>
          <div class="sales-board-line">
              <div class="sales-board-line-left">&nbsp;</div>
              <div class="sales-board-line-right">
                  <div class="button" @click="showPayDialog">
                    
                  </div>
              </div>
          </div>
      </div>
      <div class="sales-board-des">
        <h2></h2>
        <p>2020



</p>
      </div>
      <my-dialog :is-show="isShowPayDialog" @on-close="hidePayDialog">
        <table class="buy-dialog-table">
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
          <tr>
            <td>{{ buyNum }}</td>
            <td>{{ versionList.label }}</td>
            <td>{{ time.label }}</td>
            <td>
              <span v-for="(item,i) in versions" :key="i">{{ item.label }}</span>
            </td>
            <td>{{ price }}</td>
          </tr>
        </table>
        <h3 class="buy-dialog-title"></h3>
        <bank-chooser @on-change="onChangeBanks"></bank-chooser>
        <div class="button buy-dialog-btn" @click="confirmBuy">
          
        </div>
      </my-dialog>
      <my-dialog :is-show="isShowErrDialog" @on-change="hideErrDialog">
        
      </my-dialog>
      <check-order :is-show-check-dialog="isShowCheckOrder"
      :order-id="orderId" @on-close-check-dialog="hideCheckOrder"></check-order>
  </div>
</template>

<script>
import VMultiply from '../../components/multiply-chooser'
import VCounter from '../../components/counter'
import VChooser from '../../components/chooser'
import Dialog from '../../components/dialog'
import BankChooser from '../../components/bankChooser'
import CheckOrder from '../../components/checkOrder'
import _ from 'lodash'
export default{
  components:{
    VMultiply,
    VCounter,
    VChooser,
    BankChooser,
    CheckOrder,
    MyDialog:Dialog
  },
  data () {
    return{
      buyNum:0,
      versionList:[],
      time:{},
      price:0,
versionList: [
        {
          label: '',
          value: 0
        },
        {
          label: 'pdf',
          value: 1
        },
        {
          label: '',
          value: 2
        },
        {
          label: '',
          value: 3
        }
      ],
      timeList :[
        {
         label:'',
         value:0   
        },
        {
         label:'',
         value:1 
        },
        {
         label:'',
         value:2 
        }
      ],
      isShowPayDialog: false,
      bankId:null,
      isShowCheckOrder:false,
      isShowErrDialog:false
    }
  },
  methods: {
    onParamChange (attr, val) {
      this[attr] = val
      this.getPrice()
    },
    getPrice () {
      let buyVersionsArray = _.map(this.versions,
      (item) => {
        return item.value
      })
      let reqParams = {
        buyNumber: this.buyNum,
        time: this.time.value,
        version: buyVersionsArray.join(',')
      }
      this.$ajax.get('/api/getPrice', reqParams)
      .then((res) => {
       this.price=res.data.amount
       console.log(res)
      })
    },
    showPayDialog () {
      this.isShowPayDialog = true
    },
    hidePayDialog () {
      this.isShowPayDialog=false
    },
    hideErrDialog () {
      this.isShowErrDialog=false
    },
    hideCheckOrder(){
      this.isShowCheckOrder=false
    },
    onChangeBanks (bankObj) {
      this.bankId=bankObj.id
    },
    confirmBuy () {
      let buyVersionsArray = _.map(this.versions,
      (item) => {
        return item.value
      })
      let reqParams = {
        buyNumber: this.buyNum,
        versionList: this.versionList.value,
        time: this.time.value,
        version: buyVersionsArray.join(','),
        bankId:this.bankId
      }
      this.$ajax.get('/api/createOrder', reqParams)
      .then((res) => {
       this.orderId=res.data.orderId
       this.isShowCheckOrder=true
       this.isShowPayDialog=false
      },(err)=>{
       this.isShowBuyDialog=false
       this.isShowErrDialog=true
      })
    }
  },
  mounted () {
    this.buyNum=1,
    this.versions=[this.versionList[0]]
    this.time=this.timeList[0]
    this.getPrice()
  }
}

</script>
<style>

</style>


